.socials-wrapper {
  background-color: #dfb4fe;
  position: absolute;
  top: 0;
  left: 50%;
  max-width: 35%;
  width: 100%;
  height: 40.25rem;
  margin-left: -17.5%;
  display: flex;
  justify-content: center;
}

.socials-list {
  display: flex;
  padding-top: 2.5rem;
}
.socials-list--item {
  list-style: none;
  font-size: 1.5rem;
  font-weight: 800;
  margin-right: 1.875rem;
  cursor: pointer;
}

.socials-list--item a:hover {
  background-color: #f3d772;
}

.socials-list--item:last-child {
  margin-right: 0rem;
}
.socials-list--item a {
  padding-bottom: 0.3rem;
  text-decoration: none;
  color: #000;
  transition: 0.3s ease-in-out;
}
